<!--公用组件：
 /**
  * @desc TheFragment
  * @module @/components
  * @desc 组件描述：首页使用的碎片
  * @author zkhq
  * @date 2020/2/27 12:02
  * @param {Object} [title]    - 参数说明
  * @param {String} [columns] - 参数说明
  * @example 调用示例
  *  <hbTable :title="title" :columns="columns" :tableData="tableData"></hbTable>
  */
-->
<template>
  <canvas ref="canvas" class="canvas"></canvas>
</template>

<script>
// import
// import { init, clear, fun1, fun2 } from './js/index2'
// import { init, clear, fun1 } from './js/index2'
import { init, clear, fun1 } from './js/index'
export default {
  name: 'TheFragment',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
    }
  },
  props: {
    // id: {
    //   default: '',
    //   type: String
    // }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
    // 初始化碎片
    init(this.$refs.canvas)
    // 2秒后，切换为状态2
    setTimeout(function () {
      fun1()
    }, 2000)
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
    clear()
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
  }
}
</script>

<style scoped>
.canvas{
  width: 100% !important;
  height: 100% !important;
}
</style>



